.app__menu{
    display: flex;
    flex-direction: column; 
    justify-content: flex-start;
    background-color: rgb(238, 235, 221);
    height: 100%;
    overflow: auto;
}

.app__menu-header{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 3rem 0 0 3rem;
    max-width: 100%;
}

.app__menu-header h1{
    font-size: 50px;
    letter-spacing: 0.04rem;
    margin-bottom: 1rem;
}

.header-content{
    display: flex;
    flex-direction: column;
}

.header-p p{
    letter-spacing: 0.04rem;
}

.header-button{
    margin-right: 3rem;
}

.header-button button{
    font-size: 25px;
    letter-spacing: 0.04rem;
    height: 5rem;
    width: 16.5rem;
    border-radius: 12px;
    cursor: pointer;
    background-color: rgb(208, 187, 150);
    border: none;
    transition: 0.2s ease;
}

.header-button button:disabled, .header-button button[disabled]{
    opacity: 0.6;
    width: 14rem;
    height: 4rem;
    font-size: 20px;
    pointer-events: none;
}

.header-button button:hover{
    outline: 4px solid rgb(148, 131, 101);
}

.header-button button:disabled:hover, .header-button button[disabled]:hover{
    outline: none;
}

.app__menu-dash{
    background-color: black;
    height: 3px;
    width: 30%;
    margin-left: 3rem;
    margin-top: 2rem;
}

.app__menu-img{
    display: flex;
    position: relative;
    height: 100%;
    align-items: flex-start;
    padding-top: 2rem;
}

.app__menu-img_container{
    display: flex;
    flex-direction: row;
    width: 100%;
    overflow-x: hidden;
    overflow-y: hidden;
    align-items: center;
    padding: 1rem 3rem; 
}

.app__menu-img_container:hover{
    overflow-x: scroll;
    scroll-snap-type: x mandatory;
}

.app__menu-img_card{
    position: relative;
    max-width: 300px;
    max-height: 300px;
    margin-right: 3rem;
    scroll-snap-align: start;
    scroll-margin: 0 3rem;
    box-shadow: 0px 2px 4px 0 rgba(63, 55, 7, 0.5);
    border-radius: 10px;
}

.card-click{
    outline: 10px solid rgb(208, 187, 150);
}

.app__menu-img_card > div{
    opacity: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    position: absolute;
    bottom: 0%;
    width: 100%;
    height: fit-content;
    background-color: rgba(255, 255, 255, 0.7);
    backdrop-filter: blur(10px);
    border-radius: 10px;
    padding: 1rem 0;
    transition: 0.3s ease;
}

.app__menu-img_card:hover > div{
    opacity: 1;
}

.app__menu-img_card:active{
    outline: 10px solid rgb(208, 187, 150);
}

.app__menu-img_card img{
    max-width: 300px;
    max-height: 300px;
    object-fit: cover;
    transition: 0.3s ease;
    border-radius: 10px;
}

#clicked{
    display: none;
    position: absolute;
}

#clicked:checked + label > div{
    outline: 10px solid rgb(208, 187, 150); 
}